import React from 'react'

import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Input,
  Button
} from '@chakra-ui/react'
import { useTranslation } from 'react-i18next'
import '../../i18next'


import './navbar.scss'

function Navbar() {
  const { t, i18n } = useTranslation()
  const changeLanguage = (lang) => {
    i18n.changeLanguage(lang)
  }

  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()

  return (
    <div className='navbar'>

      <span><a href="#page1">{t('')}</a></span>
      <div className='navbar-menu-help'>
        <div className='navbar-menu-help-lang'>
          <span onClick={() => changeLanguage('bel')}></span>
          /
          <span onClick={() => changeLanguage('ru')}></span>
        </div>
        <span ref={btnRef} onClick={onOpen} ></span>
      </div>
      <>
        <Drawer
          isOpen={isOpen}
          placement='right'
          onClose={onClose}
          finalFocusRef={btnRef}
          size='md'
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader></DrawerHeader>
            <DrawerBody>
              <Input placeholder='  ' />
              <div className='navbar-menu'>
              <div><a href="#aboutWe" onClick={onClose}>   </a></div>
              <div>  </div>
              <div>  ,  ?</div>
              <div> </div>
              <div> ,  </div>
              <div>  </div>
              <div> </div>
              </div>
            </DrawerBody>
            <DrawerFooter>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </>
    </div>
  )
}

export default Navbar